.EmailField {
  width: 80vw;
  max-width: 450px;
}
.EmailField header {
  position: relative;
  z-index: 4;
  padding: 20px 40px;
  border-radius: 15px 15px 0 0;
  background: white;
}
.EmailField header h1 {
  font-size: 1.44em;
  font-weight: 900;
  margin: 0;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #333;
}
.EmailField header p {
  font-size: 1em;
  margin: 0;
  margin-top: 5px;
  word-spacing: 0;
  color: gray;
}
.EmailField .EmailSection {
  position: relative;
  z-index: 2;
  left: 50%;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 90px;
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  border-radius: 0 0 15px 15px;
  -webkit-box-shadow: 0 0 100px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 100px rgba(0, 0, 0, 0.2);
}
.EmailField .AnimatedButton {
  width: 20%;
  min-width: 81px;
  background-color: #d4d4ff;
}
.EmailField .AnimatedButton span {
  line-height: 90px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 90px;
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  text-align: center;
}
.EmailField .AnimatedButton .Hide {
  margin-top: -90px;
}
.EmailField .AnimatedButton .EmailIcon {
  width: 2em;
}
.EmailField .AnimatedButton .SendIcon {
  width: 2em;
}
.EmailField .AnimatedButton .SendButton:hover {
  background: yellow;
}
.EmailField form {
  position: relative;
}
.EmailField form input {
  font-size: 100%;
  width: 80%;
  margin: 0;
  padding: 20px 40px;
  color: #8f8fd6;
  border: 0;
  background: #ffffff;
}
.EmailField form input:focus {
  outline: none;
}
.EmailField form input::-webkit-input-placeholder {
  font-weight: 100;
  color: #8f8fd6;
}
.EmailField form input:-ms-input-placeholder {
  font-weight: 100;
  color: #8f8fd6;
}
.EmailField form input::placeholder {
  font-weight: 100;
  color: #8f8fd6;
}



