/* Make the NEXT element in the page give us space. */
.Navbar {
  position: fixed;
  z-index: 1;
  top: -80px;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  -webkit-animation: appear 1s ease-in forwards;
          animation: appear 1s ease-in forwards;
  border-bottom: 1px solid black;
}
@-webkit-keyframes appear {
  0% {
    top: -60px;
  }
  100% {
    top: 0;
  }
}
@keyframes appear {
  0% {
    top: -60px;
  }
  100% {
    top: 0;
  }
}



