.Contact {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100vw;
  height: 90vh;
  color: white;
}
.Contact .Header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100vw;
  height: 20vh;
  text-align: center;
}
.Contact .Header hr {
  width: 45%;
}
.Contact .Header h1 {
  font-size: 5em;
  font-weight: bold;
  margin-right: 2em;
  margin-left: 2em;
  color: dodgerblue;
  text-shadow: 0 5px 5px black;
}
.Contact .Header h2 {
  font-size: 2em;
}
.Contact .Header h3 {
  font-size: 1.5em;
}
@media screen and (max-width: 900px) {
  .Contact .Header h1 {
    font-size: 2em;
  }
  .Contact .Header h2 {
    font-size: 1.35em;
  }
  .Contact .Header h3 {
    font-size: 1.26em;
  }
}



